import React, { Component } from 'react'
import {Line} from 'react-chartjs-2'
import axios from 'axios'

export default class Chart extends Component {
    constructor(props){
        super(props)
        this.data = ''
        this.state = {
            chartData:{
                labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
                datasets: [
                    {
                        label: '2021 Sales',
                        data: [...this.data],
                        backgroundColor: "green",
                        borderColor: 'black'
                    }
                ],
            }
        }
    }

    componentDidMount(){
        axios.get('/getchartdata')
        .then((res) => {
            this.setState(prevState => ({
                chartData:{
                    labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
                    datasets: [
                        {
                            label: '2021 Sales',
                            data: [res.data.jan,res.data.feb,res.data.mar,res.data.apr,res.data.may,res.data.jun,res.data.jul,res.data.aug,res.data.sep,res.data.oct,res.data.nov,res.data.dec],
                            backgroundColor: "red",
                            borderColor: 'pink'
                        }
                    ],
                }
            }))
            this.data = [res.data.jan,res.data.feb,res.data.mar,res.data.apr,res.data.may,res.data.jun,res.data.jul,res.data.aug,res.data.sep,res.data.oct,res.data.nov,res.data.dec]
            console.log(this.data)
        })
        console.log(this.state.chartData.datasets[0].data)
    }

    render() {
        return (
            <div className='chart'>
                <Line 
                    data={this.state.chartData}
                    options={{
                        maintainAspectRatio: true
                    }}
                />
            </div>
        )
    }
}
